$lato: "Lato", sans-serif;
$mont: "Montserrat", sans-serif;
$oswald: "Oswald", sans-serif;
$ralewey: 'Raleway', sans-serif;

$blue1: #2a5697;
$blue2: #4a78c3;

$orange1: #ef958c;
$orange2: #f3b07b;

$mix1:#fafffa;

html,
body {
  width: 100%;
  height: 100%;
  background: $mix1;
  font-family: $lato;
  overflow-x: hidden;
}
.head{
  color:$blue1;
}
#root {
  width: 100%;
  height: 100%;
  background-color: $mix1;
}
.back{
  background-color:#fafffa !important;
  .dot{
    width:10px;
    height:10px;
    border-radius:50%;
    background-color:black;
  }
  .text-category{
    font-family:'Raleway',sans-serif;
    font-size:30px;
    font-weight:600;
  }
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Raleway', sans-serif;
} 
.scroll-menu-arrow {
  padding: 1rem 1.2rem;
  &:focus {
    outline: none !important;
  }
  button {
    background: $orange2;
    color: #fff;
    border-radius: 50%;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    width: 3rem;
    height: 3rem;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    &:focus {
      outline: none !important;
    }
  }
  .blue button {
    background: #fff;
    color: $blue2;
    border: 1px solid $blue2;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
  }
}
.today-current{
	background-color: white;
}
.display-4{
	color:$blue1;
}
.dialog{
