.btn-nav{
  background-color:$mix1;
  height:50px;
  border-width:0px;
  ul{
  list-style-type:none;
  }
  .span1{
    width:22px; 
    margin-bottom:4px;
    height:6px;
    background-color:black;
  }
  .span1:before{
  content: "";
   width: 1em;
  height: 0.15em;
  background: black;
  box-shadow: 
    0 0.25em 0 0 black,
    0 0.5em 0 0 black;
  }
}

.btn-nav:active:focus,
.btn-nav:focus , .menu-item-wrapper:focus {
  outline: 0 !important;
  outline-offset: 0  !important;
  background-image: none  !important;
  -webkit-box-shadow: none !important;
  box-shadow: none  !important;
}


.box-shadow-menu {
  position: relative;
  padding-left: 1.25em;
}
.box-shadow-menu:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.25em;
  width: 1em;
  height: 0.15em;
  background: black;
  box-shadow: 
    0 0.25em 0 0 black,
    0 0.5em 0 0 black;
}

.nav-dialog{
  position:absolute;
  right:0;
  height:100%;
  width:85vw;
  z-index:10;
  color:black;
  background-color:$mix1;
  ul{
    list-style-type:none;
  }
  li{
  font-size:15px;
  margin-right:5px;
  text-align:right;
  font-weight: 600;
  font-family: "Raleway",sans-serif;
  color:black !important;
  }
}
.fixed-top{
  z-index:25 !important;
}
.card-expanded{
  z-index:30;
  position:fixed;
  top:0;
  width:100%;
  height:100%;
  overflow-y:scroll;
  overflow-x:hidden;
  background-color:$mix1;
  color:black;
  .img{
    margin-left:10vw;
    margin-right:10vw;
    .close-btn{
    position:absolute;
    top:0; 
    font-family:'Raleway',sans-serif;
    font-weight:600;
    }
    .title-overlay{
    font-weight:600;
    position:absolute;
    top:26vh;
    color:white;
    }
    .img-tag{
      height:50vh;
      width:100%;
      z-index:0;
    }
  .text_card{
    .subTitle {
      font-size:25px;
      color:black;
    }
    .content{
      font-size:20px;
      color:black;
      width:100vw;
      padding:3px;
      height:0vh;
    }
  }
  }
  
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
  background:#ddd; 
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: black; 
}

@media (max-width:600px){
  .card-expanded{
    .img{
      margin:0;
      .img-tag{
      height:50vh;
      }
      .text_card{ 
      }
    }
  }
}


.loader {
  border: 10px solid #e6e6e6;
  border-radius: 50%;
  border-top: 13px solid black;
  width: 90px;
  margin:auto;
  height: 90px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
