$blue1: #2a5697;
$blue2: #4a78c3;
$orange1: #ef958c;
$orange2: #f3b07b;

.blogCard:hover{
  cursor:pointer;
}
.blogCard {
  overflow: hidden;
  border-radius: 10px;
  text-decoration: none;
  position: relative;
    p {
    font-size: 1rem !important;
  }
  .authorImg {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    margin-right: 0.8rem;
  }
  &.all {
    width: 34rem;
    overflow:visible;
    margin:auto;
    margin-top:3rem;
    .row > div {
      position: relative;
      height: 20rem;
      padding-left: 0 !important;
    }
    .card-body {
      padding-top: 3rem;
      height:17rem;
    }
    .orange-bar {
      position: absolute;
      top: 0;
      right: 0;
      width: 10rem;
      height: 100%;
      background: $orange2;
      z-index: 1;
    }
    .thumbnail {
      position: absolute;
      top: 45%;
      left: 45%;
      box-shadow: 3px 3px 14px -1px rgba(0, 0, 0, 0.5);
      transform: translateX(-50%) translateY(-50%) scale(1.6);
      width: 11rem;
      border-radius: 10px;
      z-index: 2;
    }
    .left > ul {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      list-style: none;
      padding-left:2rem;
      li {
        display: inline;
        padding: 0.4rem 1rem;
        margin-right: 0.4rem;
        background: rgba($blue2, 0.5);
        color: #fff;
        border-radius: 25px;
      }
    }
    .metatag {
      position: absolute;
      top: 0.1rem;
      left: 0;
      padding: 0.6rem 0.9rem;
      color: $orange1;
      z-index: 2;
    }
    .lang {
      position: absolute;
      top: 0.4rem;
      right: 1rem;
      background: #999;
      color: #fff;
      padding: 0.3rem 1.1rem;
      border-radius:5px;
    }
    .card-title, .card-subtitle {
        color:$blue2;
    }
    .card-subtitle {
        font-size:1.2rem!important;
    }
    .card-footer {
        background:none;
        border:none;
        color:$blue2;
    }
  }
  &.featured {
    width: 42vw;
    margin:auto;
    .metatag {
      position: absolute;
      top: 0;
      left: 0;
      padding: 0.6rem 0.9rem;
      color: #fff;
      z-index: 2;
    }
    .lang {
      position: absolute;
      top: 0;
      right: 0;
      padding: 0.6rem 0.9rem;
      color: #fff;
      z-index: 2;
    }
    .card-img-overlay {
      color: #fff;
      padding-top: 10.5rem;
      z-index: 3;
      p {
        color: #ffffffdd;
      }
    }
    .fadeOverlay {
      position: absolute;
      bottom: 3rem;
      left: 0;
      width: 100%;
      height: 10rem;
      padding: 0.6rem 0.9rem;
      color: #fff;
      z-index: 2;
    }
    .card-footer {
      color: blue;
      ul {
        list-style: none;
        display: inline;
        padding: 0 !important;
        li {
          display: inline;
          padding: 0.1rem 0.9rem;
          margin-left: 0.6rem;
          background: #ffffff44;
          border-radius: 25px;
        }
      }
    }
  }
  &.latest {
    width: 30vw;
    padding-top: 17rem;
    margin:auto;

    .lang {
      position: absolute;
      top: 0;
      right: 0;
      padding: 0.6rem 0.9rem;
      background: #00000055;
      color: #fff;
      z-index: 2;
    }
    .metatag {
      position: absolute;
      top: 14rem;
      height: 3rem;
      width: 100%;
      left: 0;
      padding: 0.6rem 0.9rem;
      color: #fff;
      background: $orange1;
      z-index: 2;
    }
    ul.tags {
      list-style: none;
      padding: 0;
      position: absolute;
      top: 14.7rem;
      height: 3rem;
      width: 70%;
      left: 31%;
      z-index: 2;
      color: #ffffffdd;
      li {
        display: inline;
        padding: 0.3rem 1.1rem;
        margin-left: 0.4rem;
        background: #ffffff44;
        border-radius: 25px;
        font-size: 0.8rem;
      }
    }
    .card-img-top {
      height: 35vh;
      width: inherit;
      position: absolute;
      top: 0;
      left: 0;
    }
    .card-footer {
      background: #fff;
      color: $blue2;
      border: none;
    }
  }
}

@media screen and  (max-width: 769px) {
    .blogCard {
        .authorImg {
            width: 1.5rem;
            height: 1.5rem;            
          }
        &.all {
            width:75vw;
            .thumbnail {
                width:32vw;
            }
            .row > div {                
                height: 22rem;
              }
        }
        &.featured{
        width:80vw;
        }
    }
}
  @media (max-width: 450px) { 
    .blogCard{
      &.latest{
        width:80vw;
      }
    }
  }
   @media (min-width: 460px) and (max-width: 780px) {
   .blogCard{
    &.latest{
      width:60vw;
    }
   }
  }

 
#blogThumb{
  height:23rem;
}